import React, { ReactElement, useState, useCallback, useEffect, useContext, useMemo, useRef, RefObject, useReducer } from 'react';
import styles from './css/dashboard.module.css';
import { Team, ProfileData, TournamentStatus, Player, Result } from '../../interfaces';
import { GET, POST } from '../../utils/requests';
import * as regexes from '../../utils/regexes';
import Info from '../../user/Info';
import { AlertContext } from '../../Contexts';
import { TournamentSettings } from '../../resources/settings';
import { capitalise, px } from '../../utils/prototype';
import PlayerData from '../../models/Player';
import { TieBreaks } from '../../resources/tiebreaks';
import { Live, Pairings } from '../';
import Announcements from '../Announcements';
import { isMobile } from '../../utils/auth';
import cx from 'classnames';
import { getFlag } from '../../utils/prototype';
import { Link } from 'react-router-dom';
import { useMatches, useDisplay, useSidebar } from '../Pairings/hooks';
import { Game } from '../../resources/result';
import Row from '../Pairings/Row';
import { LoginContext } from '../../Contexts';
import { convertPoolDisplay } from '../../utils/prototype';
import TournamentInfo from './TournamentInfo';

export interface DashboardProps {
	status: TournamentStatus
	settings: TournamentSettings
	players: PlayerData[]
	loaded: {[key: string]: boolean}
	round: number;
	isLive: boolean;
	direction: string;
	id: string;
	profile: ProfileData;
	playerDict: Map<string, Player>
	
	pop: (elem: HTMLDivElement | null) => void

	announcements: {
		announcement: string;
		createdAt: string;
		updatedAt: string;
	}[]
}

export default function Dashboard(props: DashboardProps):ReactElement {

	// Retrieve tournament organiser profile data
	const [profile, setProfile] = useState(null as ProfileData & Team | null);
	const updateProfile = useCallback(async () => {
		if (!regexes.username.test(props.status.user)) return setProfile(null);
		GET({ url: px('user', props.status.user) })
			.then(setProfile)
			.catch(() => setProfile(null));
	}, [props.status.user, setProfile]);
	useEffect(() => {
		updateProfile();
	}, [updateProfile]);

	const setField = useCallback((k: string, v: any) => POST({
		url: '/profile/updateAccount',
		data: {
			account: k,
			data: v
		}
	}).then(updateProfile), [updateProfile]);

	const setAlert = useContext(AlertContext);
	const updateBio = useCallback(() => {
		setAlert({
			title: 'Add a bio',
			message: 'Keep it short and sweet. Maximum length 280 characters.',
			type: 'prompt',
			prompt: {
				type: 'textarea',
				defaultValue: profile?.bio || '',
				allowNull: true,
				additionalInputProps: {
					maxLength: 280
				}
			},
			resolve: (v: string) => setField('bio', v)
		});
	}, [setAlert, setField, profile]);

	type FloatTypes = '' | 'tie breaks' | 'prizes' | 'description';
	const [floatContainer, setFloat] = useReducer((state: FloatTypes, action: FloatTypes) => state === action ? '' : action, '');
	
	const tieBreaks = useMemo(() => {
		let entries = Object.entries(TieBreaks);
		if (props.settings.tieBreaks) {
			let arr = props.settings.tieBreaks.split(',');
			entries = entries.sort((a, b) => arr.indexOf(a[0]) - arr.indexOf(b[0]));
		}
		return entries;
	}, [props.settings.tieBreaks]);

	const [showMore, setMore] = useState(false);

	const headers = ['Rank', 'Fed','Team'];

	// Generates the table rows of players (teams) in standings order
	const renderRows:any = useMemo(() => {
		if (!props.players.length) return [];  // No players (teams)
		let rowsData:[number, string, string, string][] = [];
		// Iterating through players
		for (let i = 0; i < props.players.length; i++) {
			let p = props.players[i];
			// Skip player if tournament format is pool-like or player is a bye
			if (p.id === 'bye') continue;
			// Push to a players array
			rowsData.push([i+1, p.nationality, p.firstName, p.id ? '/team/'+ p.id : '']);
		}

		const x = rowsData.map(([i, nationality, name, link]) => 
			<>
				<tr key={i} className={cx(styles.tr, { even: i % 2 === 0, odd: i % 2 })}>
					<td 
						className={ cx(styles.cell, 
							{ [styles.moreHidden]: !showMore && !isMobile(),
								hidden: false})}
					> 
						{i} 
					</td>
					
					<td className={ cx(styles.cell, styles.flag,
						{ [styles.moreHidden]: !showMore && !isMobile(),
							hidden: false})}>
						{ getFlag(nationality) ? getFlag(nationality) : nationality }
					</td>
					<td className={ cx(styles.cell, 
						{ [styles.moreHidden]: !showMore && !isMobile(),
							hidden: false})}>
						<Link to={link}>
							{name}
						</Link>
					</td>
				</tr>
			</>);

		// Formatting rows for rendering
		return x;
	}, [props.players]);

	const [results, setResults] = useState([] as Result[]);
	const [gameDict, setGameDict] = useState({} as {[key: string]: Game[]});
	const { getName } = useDisplay(props.settings);
	const { isLoggedIn } = useContext(LoginContext);
	const [loaded, hasLoaded] = useState(false);
	const headerRow = useRef() as RefObject<HTMLTableRowElement>;

	const {
		matches, matchDict, setMatch, populatematchDict,
		fetchedMatch, fetchNext, hasFetched, beforeSetResults
	} = useMatches({ playerDict: props.playerDict, results, settings: props.settings });

	const {
		searched, expanded, visible,
		setSearched, setExpanded, setVisible
	} = useSidebar();

	const fetchGames = useCallback((ids?: string[]) => {
		return GET({
			url: ['', 'tournament', props.id, 'getPairings'].join('/'),
			params: {
				round: props.round,
				ids: ids?.join(','),
				games: true
			}
		}).then(arr => setGameDict(arr.reduce((acc: {[key: string]: Game[]}, curr: { id: string, games: Game[]}) => {
			acc[curr.id] = curr.games;
			return acc;
		}, {})));
	}, [props.id, props.round, setGameDict]);

	useEffect(() => {
		beforeSetResults();
		setResults([]);
	}, [setResults, beforeSetResults, props.round]);
	
	useEffect(() => {
		populatematchDict(results, props.round);
	}, [populatematchDict, results]);
	useEffect(() => {
		fetchNext();
	}, [fetchNext]);

	useEffect(() => {
		updateProfile();
	}, [updateProfile]);

	useEffect(() => {
		GET({
			url: '/tournament/' + props.id + '/getPairings',
			params: { round: props.round }
		})
			.then((data) => {
				setResults(data);
				hasLoaded(true);
				return data;
			})
			.then((data: Result[]) => {
				if (window.location.hash) return;
				if (props.round >= props.status.round) return;
				//fetchGames().then(() => setExpanded(data.filter(m => m).map(m => m.id)));
			})
			.catch(() => {});
	}, [setResults, props.round, props.id, fetchGames, props.status.round]);

	const poolDistribution = useMemo(() => results.reduce((acc, curr) => {
		let pool = curr.pool || 0;
		if (!acc[pool]) acc[pool] = 0;
		acc[pool]++;
		return acc;
	}, [] as number[]), [results]);

	const rows = useCallback((pool: number) => {
		return Object.values(matchDict).sort((a, b) => {
			if (!a) return -1;
			if (!b) return 1;
			return a.board - b.board;
		}).map((match, i) => {
			if (!match) return null;
			if (match.isFinished) return null;
			if (match.id in gameDict) match.games = gameDict[match.id];
			let displayTime = match.startTime || match.registrationTime;
			let date = new Date(displayTime || 0).toString();
			let data = [
				getName(match.w),
				match.scores[0] ?? ((match.isLive || match.isFinished) && match.teams && (match.w?.chessCom || match.b?.chessCom) ? match.teams[0].score : ''),
				getName(match.b),
				match.scores[1] ?? ((match.isLive || match.isFinished) && match.teams && (match.w?.chessCom || match.b?.chessCom) ? match.teams[1].score : ''),
				displayTime ? date.slice(0, 10) + '\n' + date.slice(16, 21): '',
				match.link,
				match.w ? match.w.nationality : '',
				match.b ? match.b.nationality : ''
			] as string[];

			return <Row
				key={['row', match.board].join('.')}
				i={i}
				{...{ data, match, pool, setMatch, setExpanded, fetchGames }}
				setFinished={hasFetched}
				round={props.round}
				fetch={fetchedMatch ? fetchedMatch.id === match.id : false}
				expanded={expanded.includes(match.id)}
				highlight={isLoggedIn && match.w && match.b && (match.w.id === profile?.username || match.b.id === profile?.username || profile?.teams?.some(t => t && (t.id === match.w.teamId || t.id === match.b.teamId)))}
				hidden={(visible !== 'all' && match.status !== visible) || (!!searched.length && !searched.some(c => c.id === match.w?.id || c.id === match.b?.id))}
				settings={props.settings}
			/>;
		});
	}, [gameDict, getName, fetchedMatch, setExpanded,
		expanded, searched, matchDict, hasFetched, visible, fetchGames, setMatch,
		props.round, props.settings, profile, isLoggedIn
	]);

	return <> 
		<section className={styles.container}>

			<div className={styles.left_column}>
				{/* TODO: Remove bobbing animation when organiser is hovered */}
				<div className={styles.info_organiser_container}>

					<TournamentInfo {...props} setFloat={setFloat as (v: string) => void} />
					{/* Organiser profile card */}
					<Info
						type='user'
						id={props.status.user}
						profile={profile}
						updateProfile={updateProfile}
						updateBio={updateBio}
						className={styles.organiser_container}
					/>

					{/* Slide-In Tiebreaks Information */}
					<section className={styles.tieBreakSection}>
						<div className={['container', styles.tieBreakContainer, floatContainer ? styles.visible : ''].join(' ')}>
							<div>
								<div className={styles.tieBreakTitle} onClick={() => setFloat('')}>
									{floatContainer.toUpperCase()}
								</div>
								<div className={styles.tieBreaks}>
									{floatContainer === 'tie breaks' ?
										tieBreaks.map(([, v], i) => {
											if (!v) return null;
											return (
												<>
													<div key={[v, i, '1'].join(' ')}>#{i + 1}</div>
													<div key={[v, i, '2'].join(' ')}>{v.name}</div>
												</>
											);
										}) :
										floatContainer === 'description' ?
											props.settings.description :
											null
									}
								</div>
							</div>
						</div>
					</section>

				</div>

				
				{Object.keys(matchDict).length !== 0 && matchDict.constructor === Object ? 
					<div className={styles.featured}>
						<h1 className={styles.header}>Upcoming Matches </h1>
						{(() => {
							let tables = [];
							let hasRefed = false;
							for (let i = 0; i < poolDistribution.length; i++) {
								if (!poolDistribution[i]) continue;
								tables.push(
									<section key={['table', i].join('.')} className={cx({
										still: searched.length,
										['animated-' + props.direction]: results.length
									}, styles.table, 'scrollable')}>
										{i ? <div className={styles.pool}>Pool {convertPoolDisplay(i, props.settings.poolDisplay)}</div> : null}
										<table className={cx('download-table', 'full-width', 'pairings')}>
											<thead>
												<tr ref={hasRefed ? null : headerRow}>
													<td style={{width: '5%'}}>Fed</td>
													<td style={{width: '22.5%'}}>White</td>
													<td style={{width: '12.5%'}}>Link</td>
													<td style={{width: '5%'}}></td>
													<td style={{width: '10%'}}>Start Time</td>
													<td style={{width: '5%'}}></td>
													<td style={{width: '12.5%'}}>Watch Live</td>
													<td style={{width: '22.5%'}}>Black</td>
													<td style={{width: '5%'}}>Fed</td>
												</tr>
											</thead>
											<tbody>
												{loaded && !results.length ? <tr><td colSpan={100}>Draw has not yet taken place!</td></tr> : null}                       
												{rows(i)}
											</tbody>
										</table>
									</section>
								);
								hasRefed = true;
							}
							return tables;
						})()}
					</div> : null
				}

				<div className={styles.live}>
					<Live
						streamURL={props.settings.streamURL}
						pop={props.pop}
						isLive={props.isLive}
						screenWidthProportion={5/8}
					/>
				</div>

			</div>

			<div className={styles.teams_announcements}>

				<div className={cx(styles.teams_list, 'scrollable')}>
					{/* TODO: Find way to sticky this header */}
					<h1 className={styles.header}>Registered Players </h1>
					<table className={cx('download-table full-width', styles.table, 'standings')}>
						{/* Header */}
						<thead>
							<tr>
								{headers.map((key, i) => {
									return <th
										scope='column'
										key={['column', i].join('.')}
										className={cx(styles.th)}
										id={key}
									>
										<div className={[styles.cell, styles.headerCell].join(' ')}>
											{key}
										</div>
									</th>;
								})}
								{showMore && !isMobile() ?
									<th className={cx(styles.cell, styles.spacer)} /> :
									null
								}
							</tr>
						</thead>
						{/* Main rows body */}
						<tbody>
							{
								props.players.length ?
									renderRows :
									<tr>
										<td colSpan={42}>
										No players entered yet
										</td>
									</tr>
							}
						</tbody>
					</table>
				</div>
				
				{props.announcements.length ? <div className={styles.announcements}>
					<div className={styles.header}> Announcements </div>
					<Announcements announcements={props.announcements.map((p, i) => {
						return {
							...p,
							content: p.announcement,
							id: i.toString(),
							tournamentId: props.status.id,
							author: '',
							role: 'director',
							title: '',
							link: ''
						};
					})} />
				</div> : null}
			</div>
		</section>
	</>;
}